import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Footer from '../../components/footer'

import Pic1 from '../../img/cases/01/1.jpg'
import Pic2 from '../../img/cases/01/2.jpg'
import Pic3 from '../../img/cases/01/10.gif'
import Pic4 from '../../img/cases/01/4.jpg'
import Pic5 from '../../img/cases/01/5.jpg'
import Pic6 from '../../img/cases/01/6.jpg'
import Pic7 from '../../img/cases/01/7.jpg'
import Proces from '../../img/cases/01/Work01Proces.pdf'

class Cases01 extends Component {

  componentDidMount = () => this.props.resetScroll()

  render() {
    return (
      <div className="wrapper">
        {/* TITLECARD */}
        <div className="titleCardWrapper">
          <h1 className="workNr workColor01">01</h1>
          <div className="titleCard">
            <h1>TEDxDelft</h1>
            <div className="bar"></div>
            <div className="shortInfo">
              <div className="row">
                <p className="title">Opdracht</p>
                <p className="info">Visuele Identiteit</p>
              </div>
              <div className="row">
                <p className="title">Opdrachtgever</p>
                <p className="info">Avans Hogeschool</p>
              </div>
              <div className="row">
                <p className="title">Jaar</p>
                <p className="info">2019</p>
              </div>
            </div>
            <p className="paragraph col5">Avans Hogeschool vroeg mij om een dynamische visuele identiteit voor TEDxDelft te ontwerpen. Het thema van dit fictieve evenement was "Let's Make". Ik heb een stijl ontwikkeld en deze vastgelegd in een stijlgids. Vervolgens heb ik deze stijl toegepast op twee verschillende posters en een landingpage. Ook heb ik enkele accessoires ontworpen die op een TEDx evenement niet mogen ontbreken.</p>
            {/* <Link to="/cases/01">Terug naar vorige</Link> */}
          </div>
        </div>

        {/* FIRST IMAGE + BG */}
        <div className="imgBG">
          <div className="imgCon col6">
            <img src={Pic1} alt="mockup" />
          </div>
          <div className="bg col8 workBG01"></div>
        </div>

        {/* IMG */}
        <div className="imgCon col6">
          <img src={Pic6} alt="mockup" />
        </div>
        <div className="imgCon col9">
          <img src={Pic3} alt="mockup" />
        </div>
        <div className="imgBG">
          <div className="imgCon col6">
            <img src={Pic7} alt="mockup" />
          </div>
          <div className="bg col7 workBG01"></div>
        </div>

        {/* CARD */}
        <div className="card col3">
          <div className="title">
            <h1>Bekijk de styleguide</h1>
            <div className="bar barcenter"></div>
          </div>
          <a className="button" href={Proces} target="_blank">view</a>
        </div>

        {/* IMG */}
        <div className="imgCon col6">
          <img src={Pic2} alt="mockup" />
        </div>
        <div className="imgCon col4">
          <img src={Pic4} alt="mockup" />
        </div>

        {/* TEXT AREA
        <div className="textArea">
        <blockquote>Don't think in problems, think in solutions</blockquote>
        <p>Zolang ik me kan herinneren heb ik al interesse in het maken van logo’s, websites en video’s. Naast ontwerpen en ontwikkelen heb ik ook een grote passie voor elektronische muziek en hiphop. Ik ben in mijn vrije tijd dan ook vaak te vinden achter mijn draaitafels. Verder ga ik elke vrijdag met mijn vrienden naar Scouting en speel ik regelmatig een potje pool in de stad. Om mezelf fit te houden doe ik Capoeira en neem ik dagelijks een wandeling.</p>
        <p>Ik hoop dat je nou een klein beeld hebt van wie ik ben, maar ik zou ook graag met jou kennismaken! Neem gerust contact met me op als je interesse hebt ;)</p>
        </div> */}


        {/* ENDCARD */}
        <div className="endCard">
          <div className="col4">
            <h1>Meer zien?</h1>
            <div className="bar"></div>
            <Link to="/cases/02">→ Klik hier voor het volgende project</Link>
          </div>
          <Footer />
        </div>
      </div>
    )
  }
}

export default Cases01;