import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Footer from '../../components/footer'

import Pic1 from '../../img/cases/02/1.jpg'
import Pic2 from '../../img/cases/02/2.jpg'
import Pic3 from '../../img/cases/02/3.jpg'
import Pic4 from '../../img/cases/02/4.jpg'

class Cases02 extends Component {

  componentDidMount = () => this.props.resetScroll()

  render() {
    return (
      <div className="wrapper">
        {/* TITLECARD */}
        <div className="titleCardWrapper">
          <h1 className="workNr workColor02">02</h1>
          <div className="titleCard">
            <h1>De Stad Door Mijn Ogen</h1>
            <div className="bar"></div>
            <div className="shortInfo">
              <div className="row">
                <p className="title">Opdracht</p>
                <p className="info">Posterreeks</p>
              </div>
              <div className="row">
                <p className="title">Opdrachtgever</p>
                <p className="info">Avans Hogeschool</p>
              </div>
              <div className="row">
                <p className="title">Jaar</p>
                <p className="info">2018</p>
              </div>
            </div>
            <p className="paragraph col5">Avans Hogeschool hield 29 en 30 oktober 2018 een tentoonstelling genaamd ‘De Stad Door Mijn Ogen’. Ik ben door Avans Hogeschool benaderd om hiervoor een posterreeks te ontwerpen. Het doel was om mijn visie van ’s-Hertogenbosch te verbeelden en over te brengen op de bezoekers. De posters werden tentoongesteld op het evenement en dienden tegelijkertijd als aankondiging van de tentoonstelling.</p>
            <Link to="/cases/01">Terug naar vorige</Link>
          </div>
        </div>

        {/* FIRST IMAGE + BG */}
        <div className="imgBG">
          <div className="imgCon col7">
            <img src={Pic1} alt="mockup" />
          </div>
          <div className="bg col8 workBG02"></div>
        </div>

        {/* OTHER CONTENT */}
        <div className="imgCon col4">
          <img src={Pic2} alt="mockup" />
        </div>
        <div className="imgCon col4">
          <img src={Pic3} alt="mockup" />
        </div>
        <div className="imgCon col4">
          <img src={Pic4} alt="mockup" />
        </div>

        {/* TEXT AREA
      <div className="textArea">
        <blockquote>Don't think in problems, think in solutions</blockquote>
        <p>Zolang ik me kan herinneren heb ik al interesse in het maken van logo’s, websites en video’s. Naast ontwerpen en ontwikkelen heb ik ook een grote passie voor elektronische muziek en hiphop. Ik ben in mijn vrije tijd dan ook vaak te vinden achter mijn draaitafels. Verder ga ik elke vrijdag met mijn vrienden naar Scouting en speel ik regelmatig een potje pool in de stad. Om mezelf fit te houden doe ik Capoeira en neem ik dagelijks een wandeling.</p>
        <p>Ik hoop dat je nou een klein beeld hebt van wie ik ben, maar ik zou ook graag met jou kennismaken! Neem gerust contact met me op als je interesse hebt ;)</p>
      </div> */}

        {/* CARD
        <div className="card col3">
          <div className="title">
            <h1>Want to learn more?</h1>
            <div className="bar barcenter"></div>
          </div>
          <a className="button" target="_blank" href="https://www.nu.nl">view</a>
      </div> */}

        {/* ENDCARD */}
        <div className="endCard">
          <div className="col4">
            <h1>Nog een?</h1>
            <div className="bar"></div>
            <Link to="/cases/03">→ Klik hier voor het volgende project</Link>
          </div>
          <Footer />
        </div>
      </div>
    )
  }
}

export default Cases02;